body {
  font-family: sans-serif; }
  body a {
    text-decoration: none; }
  body label {
    display: block; }
  body .container {
    border: 1px solid #DDDDDD;
    padding: 2%; }
  body .main-content {
    background-color: #EEE; }
  body nav a {
    display: inline-block;
    margin: 0.25em 1em;
    color: #CCC; }
  body .nav-top {
    font-size: 2.5vh;
    border-bottom: 1px solid #DDD; }
  body .auth-container {
    display: flex;
    justify-content: space-between; }
  body form div {
    display: inline-block; }
    body form div .add-race .comments {
      display: block;
      width: 100vw;
      color: red; }
  body .allRaces {
    padding: 1%;
    font-weight: bold; }
    body .allRaces .allRaces-race {
      font-size: 1em;
      font-weight: normal; }
  body .apis {
    height: 5vh;
    text-align: center;
    margin: 1%; }
    body .apis img {
      height: 100%;
      padding-right: 3%; }
    body .apis span {
      height: 100%;
      padding-right: 3%;
      font-size: 1em; }
  body .time-select {
    margin-right: 0.2em; }

@media only screen and (min-width: 768px) {
  .add-race .comments {
    display: inline;
    margin-left: 1%; } }
